body{
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.ipad{
  display: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@media(max-width: 768px) {
  .ipad{
    display: block;
    font-size: 24px;
    color: #fff;
  }
  .hIpad{
    display: none;
  }
  .cNav{
      flex-direction: column;
      position: absolute;
      width: 100%;
      right: 0;
      background: #4b5563;
      top: 73px;
      z-index: 9;
  }
  .cNav li{
    margin: 0;
    padding: 10px 15px;
    width: 100%;
    border-bottom: 1px solid #cccccc80;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
